import Vue from "vue";
import Vuex from "vuex";

import root from "./root";
import settings from "./settings";
import controls from "./controls";
import sudokuStrings from "./sudokuStrings";
import clues from "./clues";
import highlight from "./highlight";

Vue.use(Vuex);

const LOCAL_STORAGE_KEY = "sudokustore";

const store = new Vuex.Store({
  modules: {
    root,
    settings,
    controls,
    sudokuStrings,
    clues,
    highlight,
  },
  mutations: {
    initialiseStoreMutation(state, storedState) {
      this.replaceState(Object.assign(state, storedState));
    },
  },
  actions: {
    initialiseStoreAction: function (context) {
      // Check if the ID exists
      let storedState = JSON.parse(localStorage.getItem(LOCAL_STORAGE_KEY));
      storedState = null;
      if (storedState && storedState.sudokuStrings.initial) {
        // Replace the state object with the stored item
        context.commit("initialiseStoreMutation", storedState);
      } else {
        let str =
          ".1..8..7.8...3...9...9.1.....3...6..94.....53..8...9.....5.6...7...1...2.5..2..1.";
        context.dispatch("sudokuStrings/setInitialAction", str);
      }
    },
  },
});

store.subscribe((_mutation, state) => {
  // Store the state object as a JSON string
  localStorage.setItem(LOCAL_STORAGE_KEY, JSON.stringify(state));

  switch (_mutation.type) {
    case "root/setActiveIndexMutation":
      store.dispatch("highlight/updateIndexes", _mutation.payload);
      break;
  }
});

export { store };

